import React, { useRef, useState } from 'react'
import classes from './Mealitemforms.module.css'
import Input from '../../UI/Input'
import { useContext } from 'react'
import { AuthContext } from '../../../../shared/context/auth-context'

const Mealitemforms = (props) => {
        const[amountisvalid, setamountisvalid]= useState(true)
        const amountinputref = useRef()
        const auth= useContext(AuthContext)

    const submithandler= event=>{
        event.preventDefault()  
        const enteramount = amountinputref.current.value;
        // console.log(enteramount)
        const enteredamountnumber = +enteramount;
        // console.log(enteredamountnumber)
        if(enteramount.trim().length === 0 || 
        enteredamountnumber <1 || 
        enteredamountnumber>5){
          setamountisvalid(false);
          return;
        } 
        props.onaddtocart(enteredamountnumber)
      }
  return (
    <>
        <form className={classes.form} onSubmit={submithandler}>
            <Input 
              label='Amount'
              ref={amountinputref}
              input={{
              id:'amount',
              type:'number',
              min:'1',
              max:'10',
              step:'1',
              defaultValue:'1'
            }}/>
            <button >+Add</button>

            {auth.customerisLoggedIn && <button>Buy</button>}
            

            {!amountisvalid && <p>please enter a valid amount (1-10)</p>}
        </form>
    </>
  )
}

export default Mealitemforms
