import React, {  useEffect, useState } from 'react'
import { useParams } from 'react-router-dom';
import ErrorModal from '../../../shared/components/UIElements/ErrorModal';
import { useHttpClient } from '../../../shared/hooks/http-hook';
// import Forminput from './Forminput';
import classes from '../../../cart/component/Meals/Avilablemeal.module.css'
// import { AuthContext } from '../../../shared/context/auth-context';
import Mealitem from '../../../cart/component/Meals/mealitem/Mealitem';
import Card from '../../../shared/components/UIElements/Card';
import LoadingSpinner from '../../../shared/components/UIElements/LoadingSpinner';

const Allproductlist = () => {
  // const[ setmeal]= useState([])
  // const[isloading, setisloading]= useState(true)
  // const[httperror, sethttperror]= useState()
    

  const[loadedUsers, setLoadedUsers]= useState([])
  const { isLoading, error, sendRequest, clearError } = useHttpClient();
  // const auth= useContext(AuthContext)
  
  // const cartctx= useContext(AuthContext)
// const navigate= useNavigate()
const placeId= useParams().nid

    useEffect(() => {
        const sendrequest = async () => {
          // setIsLoading(true);       
          try {
            const responseData = await sendRequest(process.env.REACT_APP_BACKEND_URL +`/places/name/${placeId}`);
      
           setLoadedUsers(responseData.places);
            // auth.customerlogin(responseData.customer.id)
            console.log(responseData.places)

            
    const loadmeals=[]
    for(const key in responseData){
      loadmeals.push({
        id:key,
        name:responseData[key].name,
        description:responseData[key].description,
        price:responseData[key].price
      })
      // setmeal(loadmeals)
      // setisloading(false)
    }
            
          } catch (err) {}
        };
        sendrequest();
      }, [sendRequest, placeId]);

      
  if(isLoading){
    return (<section className={classes.mealsloading}>
     <p>loading...</p>
     </section>)
   }
   if(error){
     return (<section className={classes.mealserror}>
       <p>err</p>
       </section>)
   }
 

   
   if (loadedUsers.length === 0) {
    return (
      <div style={{"display":"flex", "flexWrap":"nowrap"}} className="center">
        <Card >
          <h2  style={{"color":"white", "textDecoration":"none"}}>No Product For Sale</h2>
        </Card>
      </div>
    );
  }


    // const prices =`$${loadedUsers.price.toFixed(2)}`

//     const addtocarthandler = amount =>{
// cartctx.additem({
//   id: loadedUsers.id,
//   name:loadedUsers.name,
//   amount:amount,
//   price:loadedUsers.price
// })
//     }
      
  return (
    <>
    
    <ErrorModal error={error} onClear={clearError} />
        {isLoading && <LoadingSpinner asOverlay />}

    {
        loadedUsers.map((p)=>
        <Mealitem
          key={p.id}
          id={p.id}
         productcreator={p.creator}
         image={p.image}
            name={p.name}
            title={p.title}
            description={p.description}
            price={p.price}
        />
        )
    }
    {/* <Forminput onaddtocart={addtocarthandler}/> */}
        
    </>
  )
}

export default Allproductlist
